import React, { useState } from 'react'

const Rules = ({scores, setScores, setRules}) => {

  const password = 'romeo1011'
  const [input, setInput] = useState(false)
  const [value,setValue] = useState('')

  const risk = () => {
    if(!input){
    setInput(true)}
    else{
      if(value === password){
        setScores(scores+1)
        setInput(false)
        setValue('')
      }
    }
  }

  return (
    <div onClick={() => setRules(false)} className="rules">
        <div className="rules_content" onClick={e => e.stopPropagation()}>
            <h1 style={{marginBottom: '30px'}}></h1>
            <div className="rules_text">
                <ul>
                    <li>    -      !</li>
                    <li>      2 </li>
                    <li>      2 </li>
                    <li>      </li>
                    <li>        </li>
                    <li>  ,     </li>
                </ul>
                  .    ! 
                <br /> !
                <div className="hr_rules"></div>
                <div className="row_rules">
                  {input
                    ?  [ <input value={value} onChange={(e) => setValue(e.target.value)} type='password' placeholder=''/>,
                        <button onClick={() => risk()}></button>]
                    :  [<div className="">    1 </div>,
                        <button onClick={() => risk()}></button>]
                  }
                 
                </div>
            </div> 
        </div>
    </div>
  )
}

export default Rules