import React, { useState } from 'react'
import Quest from './Quest'

const Quests = ({setScores,scores}) => {
    const [quests, setQuest] = useState([
        {quest: '   ?', answer: ' ', status: false},
        {quest: '      ?', answer: '23', status: false},
        {quest: '     ?', answer: '', status: false},
        {quest: '        ?', answer: '', status: false},
        {quest: ' : " ,  "', answer: '', status: false},
        {quest: '     ()?', answer: '', status: false},
        {quest: '    "   "?', answer: '', status: false},
        {quest: '   ?', answer: '', status: false},
        {quest: '       ?', answer: '', status: false},
        {quest: ' : "     ."', answer: ' ', status: false},
        {quest: ' ?', answer: '', status: false},
        {quest: '   , \n  - .\n  , \n  - ', answer: '', status: false},
        {quest: '      ?', answer: '', status: false},
        {quest: '   ?', answer: ' ', status: false},
        {quest: '      ?', answer: '78', status: false},
        {quest: '  ...', answer: '', status: false},
        {quest: '        ', answer: '', status: false},
        {quest: '          ?', answer: '', status: false},
    ])

  return (
    <div className='quests'>
        {quests.map((quest, index) =>
          !quest.status &&
            <Quest quests={quests} setQues={setQuest} scores={scores} setScores={setScores} key={index} index={index+1} quest={quest}/>
        )}
    </div>
  )
}

export default Quests